"use client";

import { useEffect, useState } from "react";
import { Button } from "@/components/ui/button";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { Input } from "@/components/ui/input";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Checkbox } from "@/components/ui/checkbox";
import { collection, getDocs, addDoc, updateDoc, doc } from "firebase/firestore";
import { db } from "@/lib/firebase";
import { toast } from "sonner";

interface Checklist {
  id: string;
  veiculo: string;
  centroCusto: string;
  mecanico: string;
  produtos: string[];
  itensManutencao: Array<{
    item: string;
    concluido: boolean;
  }>;
  status: "pendente" | "concluido";
  dataInicio: string;
  dataProxima: string;
  observacao?: string;
}

interface Vehicle {
  id: string;
  placa: string;
}

interface CostCenter {
  id: string;
  nome: string;
}

interface Mechanic {
  id: string;
  nome: string;
}

interface Product {
  id: string;
  nome: string;
}

export default function ChecklistPage() {
  const [checklists, setChecklists] = useState<Checklist[]>([]);
  const [vehicles, setVehicles] = useState<Vehicle[]>([]);
  const [costCenters, setCostCenters] = useState<CostCenter[]>([]);
  const [mechanics, setMechanics] = useState<Mechanic[]>([]);
  const [products, setProducts] = useState<Product[]>([]);
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [loading, setLoading] = useState(false);
  const [newChecklist, setNewChecklist] = useState({
    veiculo: "",
    centroCusto: "",
    mecanico: "",
    produtos: [] as string[],
    itensManutencao: [] as Array<{ item: string; concluido: boolean }>,
    dataInicio: "",
    dataProxima: "",
    observacao: "",
  });
  const [newItem, setNewItem] = useState("");

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    try {
      setLoading(true);
      const [
        checklistsSnap,
        vehiclesSnap,
        costCentersSnap,
        mechanicsSnap,
        productsSnap
      ] = await Promise.all([
        getDocs(collection(db, "Checklist")),
        getDocs(collection(db, "Veiculos")),
        getDocs(collection(db, "centroCusto")),
        getDocs(collection(db, "MecanicoOperador")),
        getDocs(collection(db, "Produtos"))
      ]);

      const checklistsData = checklistsSnap.docs.map(doc => ({
        id: doc.id,
        ...doc.data(),
        itensManutencao: doc.data().itensManutencao || []
      })) as Checklist[];
      setChecklists(checklistsData);

      setVehicles(vehiclesSnap.docs.map(doc => ({
        id: doc.id,
        placa: doc.data().placa
      })));

      setCostCenters(costCentersSnap.docs.map(doc => ({
        id: doc.id,
        nome: doc.data().nome
      })));

      setMechanics(mechanicsSnap.docs.map(doc => ({
        id: doc.id,
        nome: doc.data().nome
      })));

      setProducts(productsSnap.docs.map(doc => ({
        id: doc.id,
        nome: doc.data().nome
      })));
    } catch (error) {
      console.error("Error fetching data:", error);
      toast.error("Erro ao carregar dados");
    } finally {
      setLoading(false);
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      setLoading(true);
      const docRef = await addDoc(collection(db, "Checklist"), {
        ...newChecklist,
        status: "pendente",
        createdAt: new Date().toISOString(),
      });

      const newChecklistWithId: Checklist = {
        id: docRef.id,
        ...newChecklist,
        status: "pendente",
        produtos: newChecklist.produtos || [],
        itensManutencao: newChecklist.itensManutencao || []
      } as Checklist;

      setChecklists(prev => [...prev, newChecklistWithId]);
      setNewChecklist({
        veiculo: "",
        centroCusto: "",
        mecanico: "",
        produtos: [],
        itensManutencao: [],
        dataInicio: "",
        dataProxima: "",
        observacao: "",
      });
      setIsDialogOpen(false);
      toast.success("Checklist criado com sucesso");
    } catch (error) {
      console.error("Error adding checklist:", error);
      toast.error("Erro ao criar checklist");
    } finally {
      setLoading(false);
    }
  };

  const handleAddItem = () => {
    if (newItem.trim()) {
      setNewChecklist(prev => ({
        ...prev,
        itensManutencao: [
          ...prev.itensManutencao,
          { item: newItem, concluido: false }
        ],
      }));
      setNewItem("");
    }
  };

  const handleToggleItem = async (checklistId: string, index: number) => {
    const checklist = checklists.find(c => c.id === checklistId);
    if (!checklist?.itensManutencao) return;

    try {
      const updatedItems = [...checklist.itensManutencao];
      updatedItems[index] = {
        ...updatedItems[index],
        concluido: !updatedItems[index].concluido
      };

      await updateDoc(doc(db, "Checklist", checklistId), {
        itensManutencao: updatedItems,
      });

      setChecklists(checklists.map(c =>
        c.id === checklistId
          ? { ...c, itensManutencao: updatedItems }
          : c
      ));

      toast.success("Item atualizado com sucesso");
    } catch (error) {
      console.error("Error updating checklist item:", error);
      toast.error("Erro ao atualizar item");
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center h-screen">
        <div className="text-center">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-gray-900 mx-auto"></div>
          <p className="mt-2">Carregando...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h1 className="text-2xl font-bold">Lista de verificação</h1>
        <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
          <DialogTrigger asChild>
            <Button className="bg-black text-white hover:bg-gray-800">
              + Novo Checklist
            </Button>
          </DialogTrigger>
          <DialogContent className="max-w-2xl">
            <DialogHeader>
              <DialogTitle>Novo Checklist</DialogTitle>
            </DialogHeader>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <Select
                    value={newChecklist.veiculo}
                    onValueChange={(value) => setNewChecklist(prev => ({
                      ...prev,
                      veiculo: value,
                    }))}
                  >
                    <SelectTrigger>
                      <SelectValue placeholder="Veículo" />
                    </SelectTrigger>
                    <SelectContent>
                      {vehicles.map((vehicle) => (
                        <SelectItem key={vehicle.id} value={vehicle.id}>
                          {vehicle.placa}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
                <div>
                  <Select
                    value={newChecklist.centroCusto}
                    onValueChange={(value) => setNewChecklist(prev => ({
                      ...prev,
                      centroCusto: value,
                    }))}
                  >
                    <SelectTrigger>
                      <SelectValue placeholder="Centro de Custo" />
                    </SelectTrigger>
                    <SelectContent>
                      {costCenters.map((center) => (
                        <SelectItem key={center.id} value={center.id}>
                          {center.nome}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
                <div>
                  <Select
                    value={newChecklist.mecanico}
                    onValueChange={(value) => setNewChecklist(prev => ({
                      ...prev,
                      mecanico: value,
                    }))}
                  >
                    <SelectTrigger>
                      <SelectValue placeholder="Mecânico" />
                    </SelectTrigger>
                    <SelectContent>
                      {mechanics.map((mechanic) => (
                        <SelectItem key={mechanic.id} value={mechanic.id}>
                          {mechanic.nome}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
                <div>
                  <Select
                    value={newChecklist.produtos[0] || ""}
                    onValueChange={(value) => setNewChecklist(prev => ({
                      ...prev,
                      produtos: value ? [value] : [],
                    }))}
                  >
                    <SelectTrigger>
                      <SelectValue placeholder="Produtos" />
                    </SelectTrigger>
                    <SelectContent>
                      {products.map((product) => (
                        <SelectItem key={product.id} value={product.id}>
                          {product.nome}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
              </div>

              <div className="space-y-2">
                <div className="flex space-x-2">
                  <Input
                    placeholder="Item de manutenção"
                    value={newItem}
                    onChange={(e) => setNewItem(e.target.value)}
                  />
                  <Button
                    type="button"
                    variant="outline"
                    onClick={handleAddItem}
                  >
                    Adicionar
                  </Button>
                </div>
                <div className="space-y-2">
                  {newChecklist.itensManutencao.map((item, index) => (
                    <div key={index} className="flex items-center space-x-2">
                      <Checkbox checked={item.concluido} />
                      <span>{item.item}</span>
                    </div>
                  ))}
                </div>
              </div>

              <div className="grid grid-cols-2 gap-4">
                <Input
                  type="date"
                  value={newChecklist.dataInicio}
                  onChange={(e) => setNewChecklist(prev => ({
                    ...prev,
                    dataInicio: e.target.value,
                  }))}
                  required
                />
                <Input
                  type="date"
                  value={newChecklist.dataProxima}
                  onChange={(e) => setNewChecklist(prev => ({
                    ...prev,
                    dataProxima: e.target.value,
                  }))}
                  required
                />
              </div>

              <Input
                placeholder="Observação"
                value={newChecklist.observacao}
                onChange={(e) => setNewChecklist(prev => ({
                  ...prev,
                  observacao: e.target.value,
                }))}
              />

              <Button 
                type="submit" 
                className="w-full bg-black text-white hover:bg-gray-800"
                disabled={loading}
              >
                {loading ? "Salvando..." : "Salvar"}
              </Button>
            </form>
          </DialogContent>
        </Dialog>
      </div>

      <div className="grid gap-6">
        {checklists.map((checklist) => (
          <div
            key={checklist.id}
            className="bg-white rounded-lg shadow p-6"
          >
            <div className="flex justify-between items-start">
              <div>
                <div className="flex items-center space-x-4 mb-4">
                  <h3 className="font-medium">
                    {vehicles.find(v => v.id === checklist.veiculo)?.placa}
                  </h3>
                  <span className={`px-3 py-1 rounded-full text-sm ${
                    checklist.status === "concluido"
                      ? "bg-green-100 text-green-800"
                      : "bg-yellow-100 text-yellow-800"
                  }`}>
                    {checklist.status === "concluido" ? "Concluído" : "Pendente"}
                  </span>
                </div>

                <div className="space-y-2 mb-4">
                  <p className="text-sm text-gray-500">
                    Centro de Custo: {costCenters.find(c => c.id === checklist.centroCusto)?.nome}
                  </p>
                  <p className="text-sm text-gray-500">
                    Mecânico: {mechanics.find(m => m.id === checklist.mecanico)?.nome}
                  </p>
                  <p className="text-sm text-gray-500">
                    Produtos utilizados:{" "}
                    {checklist.produtos?.length > 0
                      ? checklist.produtos.map(p =>
                          products.find(prod => prod.id === p)?.nome
                        ).join(", ")
                      : "Nenhum"}
                  </p>
                </div>

                <div className="space-y-2">
                  {(checklist.itensManutencao || []).map((item, index) => (
                    <div key={index} className="flex items-center space-x-2">
                      <Checkbox
                        checked={item.concluido}
                        onCheckedChange={() => handleToggleItem(checklist.id, index)}
                      />
                      <span className={item.concluido ? "line-through text-gray-500" : ""}>
                        {item.item}
                      </span>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>
        ))}

        {checklists.length === 0 && (
          <div className="text-center py-8 text-gray-500">
            Nenhum checklist cadastrado
          </div>
        )}
      </div>
    </div>
  );
}
